.subscription-card {
  background-color: $color-default-white;
  padding: 55px 0 61px;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 1;
  border-width: 7px;
  border-style: solid;
  border-color: transparent;

  @include vp-767 {
    padding: 44px 0 48px;
  }

  &--special {
    border-image: $gradient-primary;
    border-image-slice: 1;
  }
}

.subscription-card__title {
  margin: 0 0 10px 0;
  font-size: 26px;
  font-weight: 700;
  line-height: 110.16%;
  color: $color-sapphire;

  @include vp-767 {
    font-size: 22px;
  }
}

.subscription-card__desc {
  color: $color-chambray;
  margin: 0 0 43px 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 150%;

  @include vp-767 {
    margin: 0 0 28px 0;
  }
}

.subscription-card__price {
  color: $color-egyptian-blue;
  font-size: 60px;
  font-weight: 700;
  line-height: 100%;

  @include vp-767 {
    font-size: 48px;
    margin: 0 0 0 10px;
  }
}

.subscription-card__prices-wrapper {
  margin: 0 0 50px 0;
  position: relative;

  @include vp-767 {
    margin: 0 0 43px 0;
  }
}

.subscription-card__price-wrapper {
  gap: 14px;
  position: relative;
  z-index: 0;
  padding: 37px 18px 0 18px;
  display: none;

  &--active {
    display: flex;
  }
}

.subscription-card__price-currency {
  font-size: 60px;
  color: $color-sapphire;
  font-style: normal;
  line-height: 100%;

  @include vp-767 {
    font-size: 48px;
  }
}

.subscription-card__text-shadow {
  color: $color-additional-grey;
  display: inline;
  font-size: 100px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
  opacity: 0.48;
  position: absolute;
  top: -8px;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;

  @include vp-767 {
    font-size: 80px;
    top: 0;
  }
}

.subscription-card__order-link {
  min-width: 200px;
  min-height: 64px;
  padding: 23px 42px;
}
