@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "Alumni Sans";
  font-display: swap;
  src: url("../../fonts/alumnisans-semibold.woff2") format("woff2"),
    url("../../fonts/alumnisans-semibold.woff") format("woff");
}

@font-face {
  font-style: normal;
